import type { z } from 'zod'
import { faker as f } from '@faker-js/faker'

import type { Tour } from '@/shared/types'
import { normalizeTour } from './normalizers'
import type { TourSchema } from '../schemas'

export function makeTourSchemaMock(): z.infer<typeof TourSchema> {
  return {
    id: f.number.int() as Tour['id'],
    code: f.finance.bic(),
    name: f.commerce.productName(),
    thumbnailUrl: f.image.urlLoremFlickr({ category: 'nature' }),
  }
}

export const makeTourMock = (): Tour => normalizeTour(makeTourSchemaMock())
